html, body {
    width: 100%;
    height: 100%;
    margin: 0;
    border: 0;
    display: block;
    background: #212529;
}

#board {
    display: grid;
    height: 85%;
    width: 25%;
}

#playingfield {
    display: flex;
    justify-content: center;
    height: 100%;
}

#infobox {
    display: flex;
    flex-direction: column;
}

#score {
    height: fit-content;
}

#level {
    height: fit-content;
}

#preview {
    display: grid;
    width: 25%;
}

#ready-modal {
    position: fixed;
    top: -50%;
    left: 50%;
    transform: translateX(-50%);
    margin: 50% auto;
    height: fit-content;
    width: fit-content;
    z-index: 5;
}

.codeRunCon {
    display: flex;
    position: absolute;
    right: 0%;
    bottom: 0%;
    width: 100%;
    height: 80%;
    background-color: inherit;
}

.codeRunCon #terminal {
    display: flex;
    position: absolute;
    top: 20px;
    left: 0%;
    width: 100%;
    height: 100%;
    background-color: #000000;
}

.xterm {
    position: absolute;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 100%;
    font-feature-settings: "liga";
    position: relative;
    user-select: none;
    -ms-user-select: none;
    -webkit-user-select: none;
}

.xterm.focus,
.xterm:focus {
    outline: none;
}

.xterm .xterm-helpers {
    position: absolute;
    top: 0;
    /**
       * The z-index of the helpers must be higher than the canvases in order for
       * IMEs to appear on top.
       */
    z-index: 5;
}

.xterm .xterm-helper-textarea {
    /*
       * HACK: to fix IE's blinking cursor
       * Move textarea out of the screen to the far left, so that the cursor is not visible.
       */
    border: 0px;
    position: absolute;
    opacity: 0;
    left: -9999em;
    top: 0;
    width: 0;
    height: 0;
    z-index: -5;
    /** Prevent wrapping so the IME appears against the textarea at the correct position */
    white-space: nowrap;
    overflow: hidden;
    resize: none;
}

.xterm .composition-view {
    /* TODO: Composition position got messed up somewhere */
    background: #000;
    color: #FFF;
    display: none;
    position: absolute;
    white-space: nowrap;
    z-index: 1;
}

.xterm .composition-view.active {
    display: block;
}

.xterm .xterm-viewport {
    /* On OS X this is required in order for the scroll bar to appear fully opaque */
    background-color: #000;
    overflow-y: scroll;
    cursor: default;
    position: absolute;
    right: -20px;
    left: 0;
    top: 0;
    bottom: 0;
    margin-top: auto;
}

.xterm-viewport::-webkit-scrollbar {
    background-color: var(--dark);
    width: 5px;
}

.xterm-viewport::-webkit-scrollbar-thumb {
    background: var(--highlight);
}

.xterm .xterm-screen {
    position: relative;
    width: 100% !important;
    height: 100% !important;
}

.xterm .xterm-screen canvas {
    position: absolute;
    left: 0;
    top: 0;
    width: 100% !important;
    height: 100% !important;
}

.xterm .xterm-scroll-area {
    visibility: hidden;
}

.xterm-char-measure-element {
    display: inline-block;
    visibility: hidden;
    position: absolute;
    top: 0;
    left: -9999em;
    line-height: normal;
}

.xterm {
    cursor: text;
}

.xterm.enable-mouse-events {
    /* When mouse events are enabled (eg. tmux), revert to the standard pointer cursor */
    cursor: default;
}

.xterm.xterm-cursor-pointer {
    cursor: pointer;
}

.xterm.column-select.focus {
    /* Column selection mode */
    cursor: crosshair;
}

.xterm .xterm-accessibility,
.xterm .xterm-message {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    z-index: 10;
    color: transparent;
}

.xterm .live-region {
    position: absolute;
    left: -9999px;
    width: 1px;
    height: 1px;
    overflow: hidden;
}

.xterm-dim {
    opacity: 0.5;
}

.xterm-underline {
    text-decoration: underline;
}
